.frado-service{
    &-section{
        padding: 130px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
        h2, h3, p{
            color: var(--light-color);
        }
    }
    &-table-item{
        display: flex;
        align-items: center;
        padding: 30px;
        transition: all 0.4s;
        border-bottom: 1px solid #04DA001A;
        position: relative;
        &::before{
                    width: 200vw;
                    height: 100%;
                    content: "";
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    left: -100%;
                    transition: all 0.4s;
                    opacity: 0;
                    background-color: var(--accent-bg);
               
            }
            &:hover{
                &::before{
                    opacity: 1;
                }
                .frado-service-btn{
                    border: 2px solid var(--heading-color);
                }
            }
        @include max (991px){
            display: block;
        }
        @include max (767px){
            padding: 24px;
        }
        @include max (991px){
            padding-bottom: 30px;
            border-bottom: 1px solid #181B0E1A;
            margin-bottom: 0;
        }
        &:last-child{
            border-bottom: none;
        }
        @include max (991px){
            display: block;
        }
    }
    &-table-title{
        flex: 0 0 36%;
    }
    &-table-body{
        flex: 0 0 44%;
        padding: 0 0 0 80px;
        @include max (1399px){
            padding: 0;
        }
        @include max (1399px){
            padding: 0 0 0 50px;
        }
        @include max (991px){
            padding: 15px 0;
        }
    }
    &-table-btn{
        flex: 0 0 20%;
        display: flex;
        justify-content: end;
        @include max (991px){
            justify-content: left;
        }
    }
    // end one
    &-section2{
        padding: 130px 0;
        @include max (991px){
            padding: 80px 0;
        }
        @include max (767px){
            padding: 60px 0;
        }
    }
    &-table-item-wrap{
        display: flex;
        align-items: start;
        padding: 24px;
        border-bottom: 1px solid rgba(24, 29, 20, 0.1);
        position: relative;
        @include max (991px){
            display: block;
        }
        @include max (991px){
            padding-bottom: 24px;
            border-bottom: 1px solid rgba(24, 29, 20, 0.1);
            margin-bottom: 0;
        }
        &:last-child{
            border-bottom: none;
        }
        @include max (991px){
            display: block;
        }
    }
    &-table-icon{
        flex: 0 0 20%;
       
    }
    &-table-body2{
        flex: 0 0 50%;
        h3{
            padding-bottom: 15px;
        }
        @include max (991px){
            padding: 15px 0;
        }
    }
    &-table-date{
        flex: 0 0 30%;
        display: flex;
        justify-content: end;
        @include max (991px){
            justify-content: left;
        }
    }
    // add single
    &-d-wrap{
        max-width: 1070px;
        margin: 0 auto;
    }
    &-d-thumb{
        padding-top: 80px;
        @include max (991px){
            padding-top: 60px;
        }
        @include max (767px){
            padding-top: 40px;
        }
        img{
            border-radius: 20px;
        }
    }
    &-d-content{
        h3, h4{
            padding-bottom: 15px;
        }
        
        ul{
            li{
                position: relative;
                padding-left: 15px;
                margin-bottom: 10px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    top: 15px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
        .text{
            margin-bottom: 10px;
        }
    }
    &-d-card{
        padding-top: 40px;
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-d-thumb2{
        position: relative;
        overflow: hidden;
        transition: all 0.4s;
        border-radius: 20px;
        margin-top: 24px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
            border-radius: 20px;
            &:hover{
                transform: scale(1.1);
            }
        }
    }
}




.frado-content{
    &-thumb{
        @include max (991px){
            img{
                width: 100%;
            }
            padding-bottom: 40px;
        }
        @include max (767px){
            padding-bottom: 30px;
        }
    }
}