.frado-hero{
    &-shape {
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
    }
    &-shape2 {
        position: absolute;
        top: 54%;
        z-index: -1;
        left: 0;
        @include max (1399px){
            display: none;
        }
    }
    &-shape3 {
        position: absolute;
        top: 170px;
        right: 100px;
        z-index: -1;
        @include max (1399px){
            top: 150px;
        }
        @include max (1199px){
            top: 130px;
            right: 70px;
        }
        @include max (991px){
            top: 200px;
            right: 140px;
        }
        @include max (767px){
            display: none;
        }
    }
}
