.frado-team{
    &-wraper{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        @include max (1199px){
            gap: 24px;
            grid-template-columns: repeat(2, 1fr);
        }
        @include max (767px){
            grid-template-columns: repeat(1, 1fr);
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        background-color: var(--light-color);
        border: 1px solid var(--heading-color);
        @include max (1199px){
            border-radius: 20px;
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: all 0.4s;
        }
        &:first-child{
            border-radius: 20px 0 0 20px;
            @include max (1199px){
                border-radius: 20px;
            }
        }
        &:last-child{
            border-radius: 0 20px 20px 0;
            @include max (1199px){
                border-radius: 20px;
            }
        }
        &::before{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(24, 29, 20, 0) 47.5%, #181D14 100%);
            z-index: 1;
            transition: all .4s;
        }
        &::after{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(24, 29, 20, 0) 47.5%, #181D14 100%),
            linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
            linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
            linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
            linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
            z-index: 1;
            opacity: 0;
            transition: all .4s;
        }
        &:hover{
            img{
                transform: scale(1.1);
            }
            &::before{
                opacity: 1;
            }
            &::after{
                opacity: 1;
            }
            .frado-team-content{
                bottom: 30px;
                opacity: 1;
            }
        }
    }
    &-content{
        position: absolute;
        bottom: 0;
        left: 30px;
        opacity: 0;
        z-index: 3;
        transition: all 0.4s;
        h4{
            color: var(--light-color);
            margin-bottom: 3px;
        }
        p{
            color: var(--light-color);
        }
    }
