.frado-career{
    &-wrap{
        padding: 50px;
        border-radius: 20px;
        margin-bottom: 24px;
        transition: all 0.4s;
        background-color: var(--light-color);
        border: 1px solid var(--light-color);
        box-shadow:0 4px 50px rgba(0, 0, 0, 0.05);
        &:hover{
            border: 1px solid var(--heading-color);
        }
        @include max (1199px){
            padding: 40px;
        }
        @include max (991px){
            padding: 30px;
        }
        @include max (767px){
            padding: 30px 24px;
        }
        &.wrap2{
            margin: 0 12px 24px;
        }
    }
    &-title{
        padding-bottom: 24px;
        h4{
            padding-bottom: 15px;
        }
    }
    &-content{
        ul{
            li{
                font-weight: 500;
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                img{
                    margin-right: 12px;
                    margin-top: -5px;
                }
            }
        }
    }
    &-thumb{
        margin-bottom: 24px;
    }
    // add single
    &-d-wrap{
        max-width: 1070px;
        margin: 0 auto;
    }
    &-d-meta{
        padding-top: 24px;
        ul{
            li{
                display: inline-block;
                margin-right: 24px;
                &:last-child{
                    margin-right: 0;
                }
                a{
                    color: var(--heading-color);
                    transition: all .4s;
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    &:hover{
                        color: var(--accent-color);
                        svg path{
                            fill: var(--accent-color);
                        }
                    }
                    svg path{
                        margin-right: 10px;
                        transition: all 0.4s;
                    }
                }
            }
        }
    }
    &-d-content{
        padding-right: 50px;
        @include max (1199px){
            padding-right: 20px;
        }
        @include max (991px){
            padding-right: 0;
        }
        h3, h4{
            padding-bottom: 15px;
        }
        ul{
            li{
                position: relative;
                padding-left: 15px;
                margin-bottom: 10px;
                &:last-child{
                    margin-bottom: 0;
                }
                &::before{
                    top: 15px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    content: "";
                    position: absolute;
                    border-radius: 100px;
                    background-color: var(--heading-bg);
                }
            }
        }
    }
    &-d-author-wraper{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 80px;
        flex-wrap: wrap;
        gap: 15px;
        padding-top: 60px;
        @include max (1199px){
            padding-right: 0;
        }
        @include max (991px){
            padding-top: 40px;
        }
        @include max (767px){
            padding-top: 30px;
        }
    }
    &-d-author-wrap{
        display: flex;
        align-items: center;
        gap: 12px;
    }
    &-d-author-data{
        p{
            margin-bottom: 5px;
        }
        span{
            font-size: 18px;
            line-height: 25px;
        }
    }
    &-d-author-info{
        p{
            margin-bottom: 5px;
        }
    }
    &-sidebar{
        background-color: var(--heading-bg);
        border-radius: 20px;
        padding: 30px;
        position: sticky;
        top: 120px;
        margin-left: 30px;
        @include max (991px){
            margin-left: 0;
            margin-top: 40px;
        }
        @include max (767px){
            margin-top: 30px;
        }
        .frado-main-field input{
            margin-bottom: 24px;
        }
        h4{
            padding-bottom: 15px;
            color: var(--light-color);
        }
        p{
            color: var(--light-color);
        }
    }
    &-apply-btn-wrap{
        ul{
            display: flex;
            gap: 60px;
            align-items: center;
            @include max (767px){
                display: block;
            }
            li{
                display: inline-grid;
                @include max (767px){
                    display: block;
                    margin-bottom: 15px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
                a{
                    color: var(--heading-color);
                    font-weight: 500;
                }
            }
        }
    }
}